// ждем когда загрузится DOM
$(document).ready(function() {
    // выбирает тег БАДИ и вставляем перед закрывающим тегом нашу кнопку
    $('body').append('<button id="toTop" title="Go to top"><img src="data:image/svg+xml;utf8;base64," /></button>');
    // Добавляем к нашей кнопке стили (CSS)
    $("#toTop").css({ "display": "none", "position": "fixed", "bottom": "18px", "right": "18px", "z-index": "1000", "border": "none", "outline": "none", "background": "none", "cursor": "pointer" });
    // В момент скрола в окне показываем или скрываем нашу кнопку
    $(window).scroll(function() {
        if ($(this).scrollTop() > 300) {
            $('#toTop').fadeIn();
        } else {
            $('#toTop').fadeOut();
        }
    });
    // Вешаем функцию-обработчик на действие по клику по нашей кнопке
    $('#toTop').on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({ scrollTop: 0 }, 800); // задаем анимацию, плавность
        return false;
    });





    // jquery 1 boxes
    $('.details1').click(function() {

        $('.main1').css("display", "none")
        $('.added1').fadeIn(800)
        $('.details1').css("display", "none")
        $('.backs1').fadeIn(300)
    });

    $('.backs1').click(function() {

        $('.main1').css("display", "block")
        $('.added1 ').css("display", "none")
        $('.backs1').css("display", "none")
        $('.details1').fadeIn(200)
    });
    // jquery 2 boxes
    $('.details2').click(function() {

        $('.main2').css("display", "none")
        $('.added2').fadeIn(800)
        $('.details2').css("display", "none")
        $('.backs2').fadeIn(300)
    });

    $('.backs2').click(function() {

        $('.main2').css("display", "block")
        $('.added2 ').css("display", "none")
        $('.backs2').css("display", "none")
        $('.details2').fadeIn(200)
    });
    // jquery 3 boxes
    $('.details3').click(function() {

        $('.main3').css("display", "none")
        $('.added3').fadeIn(800)
        $('.details3').css("display", "none")
        $('.backs3').fadeIn(300)
    });

    $('.backs3').click(function() {

        $('.main3').css("display", "block")
        $('.added3 ').css("display", "none")
        $('.backs3').css("display", "none")
        $('.details3').fadeIn(200)
    });
    // jquery 4 boxes
    $('.details4').click(function() {

        $('.main4').css("display", "none")
        $('.added4').fadeIn(800)
        $('.details4').css("display", "none")
        $('.backs4').fadeIn(300)
    });

    $('.backs4').click(function() {

        $('.main4').css("display", "block")
        $('.added4 ').css("display", "none")
        $('.backs4').css("display", "none")
        $('.details4').fadeIn(200)
    });
    // jquery 5 boxes


    // menu jquery
    $('.fa-bars').click(function() {
        $('.asaide__menu').addClass('active')
    });
    $('.fa-times').click(function() {
        $('.asaide__menu').removeClass('active')
    });
    // menu jquery














    $(".btns").click(function() {
        $('.nihad__prices').toggle(500)

    });

});